import React, { useState, useEffect } from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux'
import {goBack, closeModal, setStory} from "./js/store/router/actions";
import {getActivePanel} from "./js/services/_functions";
import * as VK from './js/services/VK';

import {Epic, View, Root, Tabbar, ModalRoot, TabbarItem, ModalCard, Button, withPlatform} from "@vkontakte/vkui";

import {
    AdaptivityProvider,
    ConfigProvider,
    useAdaptivity,
    AppRoot,
    SplitLayout,
    SplitCol,
    ViewWidth,
    Panel,
    PanelHeader,
    Header,
    Group,
    SimpleCell,
    ScreenSpinner,
    withAdaptivity
  } from "@vkontakte/vkui";

import {
    Icon28Menu, 
    Icon28HomeOutline,
    Icon56BlockOutline,
    Icon28BookOutline
} from '@vkontakte/icons';

import HomePanelBase from './js/panels/home/base';
import HomePanelGroups from './js/panels/home/groups';

import MorePanelBase from './js/panels/more/base';

import WorkPanelBase from './js/panels/work/base'

import MorePanelExample from './js/panels/more/example';

import HomeBotsListModal from './js/components/modals/HomeBotsListModal';
import HomeBotInfoModal from './js/components/modals/HomeBotInfoModal';

import bridge from '@vkontakte/vk-bridge';

const MODAL_CARD_NEWS = 'news';

const Exx = withPlatform(withAdaptivity(class App extends React.Component { 
    constructor(props) {
        super(props);

        this.lastAndroidBackAction = 0;
        this.showsneg = 0;
    }

    componentDidMount() {
        const {goBack, dispatch} = this.props;

        dispatch(VK.initApp());

        window.onpopstate = () => {
            let timeNow = +new Date();

            if (timeNow - this.lastAndroidBackAction > 500) {
                this.lastAndroidBackAction = timeNow;

                goBack();
            } else {
                window.history.pushState(null, null);
            }
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {activeView, activeStory, activePanel, scrollPosition} = this.props;

        if (
            prevProps.activeView !== activeView ||
            prevProps.activePanel !== activePanel ||
            prevProps.activeStory !== activeStory
        ) {
            let pageScrollPosition = scrollPosition[activeStory + "_" + activeView + "_" + activePanel] || 0;

            window.scroll(0, pageScrollPosition);
        }
    }

    render() {
        const {goBack, setStory, closeModal, popouts, activeView, activeStory, activeModals, panelsHistory, colorScheme} = this.props;
        
        let history = (panelsHistory[activeView] === undefined) ? [activeView] : panelsHistory[activeView];
        let popout = (popouts[activeView] === undefined) ? null : popouts[activeView];
        let activeModal = (activeModals[activeView] === undefined) ? null : activeModals[activeView];
        
        const homeModals = (
            <ModalRoot activeModal={activeModal}>
                <HomeBotsListModal
                    id="MODAL_PAGE_BOTS_LIST"
                    onClose={() => closeModal()}
                />
                <HomeBotInfoModal
                    id="MODAL_PAGE_BOT_INFO"
                    onClose={() => closeModal()}
                />
                <ModalCard
                    id={MODAL_CARD_NEWS}
                    onClose={() => closeModal()}
                    icon={<Icon56BlockOutline />}
                    header="    !"
                    actions={
                        <Button size="l" mode="primary" onClick={() => closeModal()}>
                         
                        </Button>
                    }
                    >
                </ModalCard>
            </ModalRoot>
        );

        return (
            <ConfigProvider transitionMotionEnabled="false" scheme={colorScheme}>
                <AdaptivityProvider>
                    <Epic activeStory={activeStory} tabbar={
                        <Tabbar>
                            
                            <TabbarItem
                                onClick={() => setStory('home', 'base')}
                                selected={activeStory === 'home'} text="">
                                <Icon28HomeOutline/>
                            </TabbarItem>

                            <TabbarItem
                                onClick={() => setStory('work', 'base')}
                                selected={activeStory === 'work'} text="">
                                <Icon28BookOutline />
                            </TabbarItem>

                            <TabbarItem
                                onClick={() => setStory('more', 'base')}
                                selected={activeStory === 'more'} text="">
                                <Icon28Menu/>
                            </TabbarItem>

                        </Tabbar>
                    }>
                        <Root id="home" activeView={activeView} popout={popout}>
                            <View
                                id="home"
                                modal={homeModals}
                                activePanel={getActivePanel("home")}
                                history={history}
                                onSwipeBack={() => goBack()}
                            >
                                <HomePanelBase id="base" withoutEpic={false}/>
                                <HomePanelGroups id="groups"/>
                            </View>
                        </Root>

                        <Root id="work" activeView={activeView} popout={popout} withoutEpic={false}>
                            <View
                                id="work"
                                modal={homeModals}
                                activePanel={getActivePanel("work")}
                                history={history}
                                onSwipeBack={() => goBack()}
                            >
                                <WorkPanelBase id="base" />
                            </View>
                        </Root>

                        <Root id="more" activeView={activeView} popout={popout} withoutEpic={false}>
                            <View
                                id="more"
                                modal={homeModals}
                                activePanel={getActivePanel("more")}
                                history={history}
                                onSwipeBack={() => goBack()}
                            >
                                <MorePanelBase id="base" />
                            </View>

                            <View
                                id="modal"
                                modal={homeModals}
                                activePanel={getActivePanel("modal")}
                                history={history}
                                onSwipeBack={() => goBack()}
                            >
                                <MorePanelExample id="filters"/>
                            </View>
                        </Root>

                    </Epic>
                </AdaptivityProvider>
            </ConfigProvider>
        );
    }
})
);


class Appf extends React.Component { 
    constructor(props) {
        super(props);

        this.lastAndroidBackAction = 0;
        this.showsneg = 0;
    }

    componentDidMount() {
        const {goBack, dispatch} = this.props;

        dispatch(VK.initApp());

        window.onpopstate = () => {
            let timeNow = +new Date();

            if (timeNow - this.lastAndroidBackAction > 500) {
                this.lastAndroidBackAction = timeNow;

                goBack();
            } else {
                window.history.pushState(null, null);
            }
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {activeView, activeStory, activePanel, scrollPosition} = this.props;

        if (
            prevProps.activeView !== activeView ||
            prevProps.activePanel !== activePanel ||
            prevProps.activeStory !== activeStory
        ) {
            let pageScrollPosition = scrollPosition[activeStory + "_" + activeView + "_" + activePanel] || 0;

            window.scroll(0, pageScrollPosition);
        }
    }

    render() {
        const {goBack, setStory, closeModal, popouts, activeView, activeStory, activeModals, panelsHistory, colorScheme} = this.props;
        const { viewWidth } = useAdaptivity();

        let history = (panelsHistory[activeView] === undefined) ? [activeView] : panelsHistory[activeView];
        let popout = (popouts[activeView] === undefined) ? null : popouts[activeView];
        let activeModal = (activeModals[activeView] === undefined) ? null : activeModals[activeView];

        const homeModals = (
            <ModalRoot activeModal={activeModal}>
                <HomeBotsListModal
                    id="MODAL_PAGE_BOTS_LIST"
                    onClose={() => closeModal()}
                />
                <HomeBotInfoModal
                    id="MODAL_PAGE_BOT_INFO"
                    onClose={() => closeModal()}
                />
                <ModalCard
                    id={MODAL_CARD_NEWS}
                    onClose={() => closeModal()}
                    icon={<Icon56BlockOutline />}
                    header="    !"
                    actions={
                        <Button size="l" mode="primary" onClick={() => closeModal()}>
                         
                        </Button>
                    }
                    >
                </ModalCard>
            </ModalRoot>
        );

        return (
            <AppRoot>
                <Root>
                <SplitLayout header={<PanelHeader separator={false} />}>
                    <SplitCol spaced={viewWidth > ViewWidth.MOBILE}>
                            <View>
                                <Epic activeStory={activeStory} tabbar={
                                    <Tabbar>
                                        
                                        <TabbarItem
                                            onClick={() => setStory('home', 'base')}
                                            selected={activeStory === 'home'} text="">
                                            <Icon28HomeOutline/>
                                        </TabbarItem>

                                        <TabbarItem
                                            onClick={() => setStory('work', 'base')}
                                            selected={activeStory === 'work'} text="">
                                            <Icon28BookOutline />
                                        </TabbarItem>

                                        <TabbarItem
                                            onClick={() => setStory('more', 'base')}
                                            selected={activeStory === 'more'} text="">
                                            <Icon28Menu/>
                                        </TabbarItem>

                                    </Tabbar>
                                }>
                                    <Root id="home" activeView={activeView} popout={popout}>
                                        <View
                                            id="home"
                                            modal={homeModals}
                                            activePanel={getActivePanel("home")}
                                            history={history}
                                            onSwipeBack={() => goBack()}
                                        >
                                            <HomePanelBase id="base" withoutEpic={false}/>
                                            <HomePanelGroups id="groups"/>
                                        </View>
                                    </Root>

                                    <Root id="work" activeView={activeView} popout={popout} withoutEpic={false}>
                                        <View
                                            id="work"
                                            modal={homeModals}
                                            activePanel={getActivePanel("work")}
                                            history={history}
                                            onSwipeBack={() => goBack()}
                                        >
                                            <WorkPanelBase id="base" />
                                        </View>
                                    </Root>

                                    <Root id="more" activeView={activeView} popout={popout} withoutEpic={false}>
                                        <View
                                            id="more"
                                            modal={homeModals}
                                            activePanel={getActivePanel("more")}
                                            history={history}
                                            onSwipeBack={() => goBack()}
                                        >
                                            <MorePanelBase id="base" />
                                        </View>

                                        <View
                                            id="modal"
                                            modal={homeModals}
                                            activePanel={getActivePanel("modal")}
                                            history={history}
                                            onSwipeBack={() => goBack()}
                                        >
                                            <MorePanelExample id="filters"/>
                                        </View>
                                    </Root>

                                </Epic>
                            </View>
                    </SplitCol>
                </SplitLayout>
                </Root>
            </AppRoot>
        );
    }
}

const App = () => {
    const { viewWidth } = useAdaptivity();

    const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popouot, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

    return (
            <AppRoot>
                <SplitLayout header={<PanelHeader separator={false} />}>
                    <SplitCol spaced={viewWidth > ViewWidth.MOBILE}>
                        <View activePanel={activePanel}>
                            <Tabbar>
                                <TabbarItem
                                    selected text="">
                                    <Icon28HomeOutline/>
                                </TabbarItem>

                                <TabbarItem
                                    text="">
                                    <Icon28BookOutline />
                                </TabbarItem>

                                <TabbarItem
                                    text="">
                                    <Icon28Menu/>
                                </TabbarItem>
                            </Tabbar>
                        </View>
                    </SplitCol>
                </SplitLayout>
            </AppRoot>
        );
};

const mapStateToProps = (state) => {
    return {
        activeView: state.router.activeView,
        activeStory: state.router.activeStory,
        panelsHistory: state.router.panelsHistory,
        activeModals: state.router.activeModals,
        popouts: state.router.popouts,
        scrollPosition: state.router.scrollPosition,

        colorScheme: state.vkui.colorScheme
    };
};


function mapDispatchToProps(dispatch) {
    return {
        dispatch,
        ...bindActionCreators({setStory, goBack, closeModal}, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Appf);
