import React from 'react';
import {connect} from 'react-redux';

import {setPage} from "../../store/router/actions";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";

import {
    Panel,
    Group,
    PanelHeader,
    CardGrid,
    Separator,
    Spacing
} from "@vkontakte/vkui";

import { ContentCard } from '@vkontakte/vkui';

import {
    closePopout, 
    openModal, 
    openPopout
} from '../../store/router/actions';

import {} from '@vkontakte/icons';

import {View} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = {
            activePanel: 'base'
        };
      }

    componentDidMount() {
        restoreScrollPosition();
    }

    render() {

        return (
            <View transitionMotionEnabled="false" activePanel={this.state.activePanel}>
                
                <Panel id="base">
                    <Spacing size={8} />
                    <PanelHeader>
                         
                    </PanelHeader>
                    <Separator />

                    <Group>
                        {/* <Placeholder
                            icon={<Icon16Recent width={56} height={56} />}
                            header=". "
                        >
                                  
                        </Placeholder> */}

                        <CardGrid size="l">
                        <ContentCard
                            subtitle=" 16.04.2021"
                            image=""
                            header="   II"
                            caption=" : 22"
                            maxHeight={150}
                        />

                        <ContentCard
                            subtitle=" 16.04.2021"
                            image=""
                            header=" "
                            caption=" : 24   ''   + 1   ' ' "
                            maxHeight={150}
                        />

                        <ContentCard
                            subtitle="  16.04.2021"
                            image=""
                            header=" "
                            caption=" : . 12, . 195"
                            maxHeight={150}
                        />

                        <ContentCard
                            subtitle=" 16.04.2021"
                            image=""
                            header=" :    "
                            caption=" : 37"
                            maxHeight={150}
                        />

                        <ContentCard
                            subtitle="  16.04.2021"
                            image=""
                            header="     "
                            caption=" : 36  198-199, . 425 (), . 426"
                            maxHeight={150}
                        />
                        </CardGrid>
                    </Group>
                </Panel>
            </View>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    setActiveTab,
    setScrollPositionByID,
    openPopout,
    closePopout,
    openModal
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);