import React from 'react';
import {connect} from 'react-redux';

import {setPage} from "../../store/router/actions";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";

import VKConnect from "@vkontakte/vk-connect";
import "@vkontakte/vkui/dist/vkui.css";

import {
    Panel,
    Group,
    PanelHeader,
    Snackbar,
    Avatar,
    SimpleCell,
    PanelHeaderBack,
    RichCell,
    Footer,
    Spacing,
    Separator
} from "@vkontakte/vkui";

import {closePopout, openModal, openPopout} from '../../store/router/actions';

import { 
    Icon16ErrorCircle,
    Icon24Home,
    Icon24Users,
    Icon24Favorite,
} from '@vkontakte/icons';

import {View} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = {
            activePanel: 'base',
            snackbar: null
        };
      }

    openBaseWithAction () {
        if (this.state.snackbar) return;
            this.setState({ snackbar:
                <Snackbar
                    onClose={() => this.setState({ snackbar: null })}
                    before={<Avatar size={22} style={{ background: 'white' }}><Icon16ErrorCircle fill="#FF0000" width={24} height={24} /></Avatar>}
                >
                    -   
                </Snackbar>
            });
    }

    componentDidMount() {
        restoreScrollPosition();
    }

    // openPopout() {
    //     this.props.openPopout(
    //         <Alert
    //             actions={[{
    //                 title: '',
    //                 autoclose: true,
    //                 style: 'cancel',
    //             }
    //         ]}
    //             onClose={() => this.props.closePopout()}
    //         >   
    //             <div style={{ 
    //                 display: 'flex',
    //                 flexDirection: 'column',
    //                 alignItems: 'center',
    //                 justifyContent: 'center',
    //                 textAlign: 'center',
    //                 padding: 32, 
    //             }}>
    //                 <Icon56GhostOutline fill="#20E5F3" width={96} height={96} />
    //                 <h1>. </h1>
    //                 <p> . ..<br/>   ,      .</p>
    //             </div>
                
    //         </Alert>
    //     );
    // }

    render() {

        return (
            <View transitionMotionEnabled="false" activePanel={this.state.activePanel}>
                
                <Panel id="base">
                    <Spacing size={8} />
                    <PanelHeader>
                        
                    </PanelHeader>
                    <Separator />

                    <Group>
                            <SimpleCell before={<Icon24Users fill="#933CA5" />} onClick={() => this.setState({ activePanel: 'sostave' })}></SimpleCell>
                            <SimpleCell before={<Icon24Home fill="#933CA5" />} onClick={() => VKConnect.send("VKWebAppAddToHomeScreen")}>   </SimpleCell>
                            <SimpleCell before={<Icon24Favorite fill="#933CA5" />} onClick={() => VKConnect.send("VKWebAppAddToFavorites").catch(VKConnect.send("VKWebAppTapticNotificationOccurred"))}>  </SimpleCell>
                    </Group>

                </Panel>

                <Panel id="sostave">
                    <Spacing size={8} />
                    <PanelHeader left={<PanelHeaderBack />} onClick={() => this.setState({activePanel: 'base'})}>
                        
                    </PanelHeader>
                    <Separator />

                    <Spacing size={18} />

                    <Group>
                        <RichCell
                            before={<Avatar size={48} />}
                            caption=""
                            after=""
                        >
                             
                        </RichCell>

                        <RichCell
                            before={<Avatar size={48} />}
                            caption=""
                            after=""
                        >
                             
                        </RichCell>

                        <RichCell
                            before={<Avatar size={48} />}
                            caption=""
                            after=""
                        >
                             
                        </RichCell>

                        <RichCell
                            before={<Avatar size={48} />}
                            caption=""
                            after=""
                        >
                             
                        </RichCell>

                        <RichCell
                            before={<Avatar size={48} />}
                            caption=""
                            after=""
                        >
                             
                        </RichCell>

                        <RichCell
                            before={<Avatar size={48} />}
                            caption=""
                            after=""
                        >
                             
                        </RichCell>

                        <Footer> 6 </Footer>
                    </Group>
                </Panel>
                
            </View>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    setActiveTab,
    setScrollPositionByID,
    openPopout,
    closePopout,
    openModal
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);
